<template>
<div class="col" >
    <spinner v-if='!renderit'/>
    <div class="card" v-else>
        <div class="card-body">
            <h5 class="card-category h5">About Me</h5>
            <p class="card-description">
                I'm a full-stack web developer living in Tunisia.
                I spend my days with my hands in many different areas of web development from back end programming (PHP, NodeJS) to front end engineering (HTML, CSS, and jQuery/Javascript), user experience and visual design.
                Currently I spend my days working in some of my projects like streams.rip ...
                I'm also available for smaller, interesting projects during evenings and weekends.
                I'm currently unavailable for projects, though feel free to get in touch if you have an idea I might be interested in.
                I'm extremely passionate about web development and design in all it's forms and helping small businesses and artists build and improve their online presence.
                Aside from web development, I enjoy spending my time on playing and watching sports (basicly soccer).
            </p>
        </div>
    </div>
</div>
</template>

<script>
import spinner from '@/components/spinner'
export default {
  components: {spinner},
  name: 'index',
  data () {
    return {
      renderit: false
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () { vm.renderit = true }, 250)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



