<template>
<div>
    <spinner v-if="!renderit"/>
    <div v-else>
        <index/>
        <skills/>
        <experiences/>
        <services/>
        <contact/>
    </div>
</div>
</template>

<script>
import spinner from '@/components/spinner'
import index from '@/components/index'
import skills from '@/components/skills'
import contact from '@/components/contact'
import experiences from '@/components/experiences'
import services from '@/components/services'
export default {
  components: {
    spinner,
    index,
    services,
    experiences,
    contact,
    skills},
  name: 'onepage',
  data () {
    if (this.$route.name === 'Onepagelight') {
      localStorage.theme = 'light'
    } else {
      localStorage.theme = 'dark'
    }
    return {
      renderit: false
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () { vm.renderit = true }, 500)
  }
}
</script>



