/* eslint-disable */
import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Skills from '@/components/skills'
import Contact from '@/components/contact'
import Experiences from '@/components/experiences'
import Onepage from '@/components/onepage'
import Services from '@/components/services'
import Themayos from '@/components/themayos'
import VueCarousel from 'vue-carousel'
import VueResource from 'vue-resource'
import BackToTop from 'vue-backtotop'

Vue.use(VueResource)
Vue.use(VueCarousel)
Vue.use(BackToTop)
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/skills',
            name: 'Skills',
            component: Skills
        },
        {
            path: '/services',
            name: 'Services',
            component: Services
        },
        {
            path: '/experiences',
            name: 'Experiences',
            component: Experiences
        },
        {
            path: '/contact',
            name: 'Contact',
            component: Contact
        },
        {
            path: '/themayos',
            name: 'Themayos',
            component: Themayos
        },
        {
            path: '/',
            name: 'Index',
            component: Index
        },
        {
            path: '/light',
            name: 'Light',
            component: Index
        },
        {
            path: '/dark',
            name: 'Dark',
            component: Index
        },
        {
            path: '/onepagedark',
            name: 'Onepagedark',
            component: Onepage
        },
        {
            path: '/onepagelight',
            name: 'Onepagelight',
            component: Onepage
        }
    ]
})


