<template>
<div class="col" >
    <spinner v-if='!renderit'/>
    <div class="card-profile" v-else>
        <div class="card">
            <h3 class="card-category h3">Themayos</h3>
            <h4 class="card-category h4">Personal Portfolio HTML Template</h4>
        </div>
        <div class="card-category row">
          <div class="col-md-6" v-for="(proj,i) in projects" :key="i">
            <div class=" card">
              <img class="card-img-top" :src="proj.img" :alt="proj.title">
              <div class="card-body">
                <h3 class="card-category h3">{{proj.title}}</h3>
                <a :href="proj.link" target="_blank" class="bttn-float bttn-warning input-group-btn">View</a>
              </div>
              <div class="card-footer"></div>
            </div>
          </div>
        </div>
    </div>
</div>
</template>

<script>
import spinner from '@/components/spinner'
export default {
  components: {spinner},
  name: 'themayos',
  data () {
    localStorage.theme = 'light'
    return {
      renderit: false,
      projects: [
        {
          title: 'Themayos',
          link: 'https://fedi.pw/#/dark',
          img: '/static/img/projects/dark.png'
        },
        {
          title: 'Themayos One Page',
          link: 'https://fedi.pw/#/onepagedark',
          img: '/static/img/projects/onepagedark.png'
        },
        {
          title: 'Themayos Light',
          link: 'https://fedi.pw/#/light',
          img: '/static/img/projects/light.png'
        },
        {
          title: 'Themayos One Page Light',
          link: 'https://fedi.pw/#/onepagelight',
          img: '/static/img/projects/onepagelight.png'
        }]
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () { vm.renderit = true }, 250)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.bttn-float {
  padding: 0.5rem 3rem;
}
img.card-img-top {
    max-height: 400px;
}
</style>



