<template>
<div class="col">
    <spinner v-if='!renderit'/>
    <div class="card-profile" v-else>
        <div class="card card-body">
            <h5 class="card-category h5">Professional skills</h5>
            <div class="card-description">
                <div class="card-body">
                    <dl class="dl-horizontal clear-mrg row">
                        <div v-for="skill in skills" :key="skill.name" class="col-md-6">
                            <div class="card-title h6 text-left"><i :class="'fab '+skill.icon"></i>   {{skill.name}}</div>
                            <div class="row">
                                <div class="col-9 col-xs-10">
                                    <div class="bar bar-sm">
                                        <div class="bar-item" data-toggle="tooltip" :title="skill.value+'%'" :style="'width:'+skill.value+'%;'"></div>
                                    </div>
                                </div>
                                <div class="col h6 text-center">{{skill.value}} %</div>
                            </div>
                        </div>
                    </dl>
                </div>
            </div>
        </div>
        <div class="card card-body">
            <h5 class="card-category h5">Additional skills</h5>
            <div class="card-description">
                <div class="card-body">
                    <dl class="dl-horizontal clear-mrg row">
                        <div v-for="skill in askills" :key="skill.name" class="col-md-6">
                            <div class="card-title h6 text-left"><i :class="'fab '+skill.icon"></i>   {{skill.name}}</div>
                            <div class="row">
                                <div class="col-9 col-xs-10">
                                    <div class="bar bar-sm">
                                        <div class="bar-item" data-toggle="tooltip" :title="skill.value+'%'" :style="'width:'+skill.value+'%;'"></div>
                                    </div>
                                </div>
                                <div class="col h6 text-center">{{skill.value}} %</div>
                            </div>
                        </div>
                    </dl>
                </div>
            </div>
        </div>
        <div class="card card-body">
            <h5 class="card-category h5">Languages</h5>
            <div class="card-description">
                <div class="card-body">
                    <dl class="dl-horizontal clear-mrg row">
                        <div v-for="skill in lang" :key="skill.name" class="col-md-6">
                            <div class="card-title h6 text-left"><i :class="'fas '+skill.icon"></i>   {{skill.name}}</div>
                            <div class="row">
                                <div class="col-9 col-xs-10">
                                    <div class="bar bar-sm">
                                        <div class="bar-item" data-toggle="tooltip" :title="skill.value+'%'" :style="'width:'+skill.value+'%;'"></div>
                                    </div>
                                </div>
                                <div class="col h6 text-center">{{skill.value == 100 ? 'Native' : skill.value+' %'}}</div>
                            </div>
                        </div>
                    </dl>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import spinner from '@/components/spinner'
export default {
  components: {spinner},
  name: 'skills',
  data () {
    return {
      renderit: false,
      skills: [
        {name: 'HTML & CSS', value: '100', icon: 'fa-html5'},
        {name: 'PHP & MySql', value: '100', icon: 'fa-php'},
        {name: 'Javascript', value: '90', icon: 'fa-js-square'},
        {name: 'Vuejs', value: '80', icon: 'fa-vuejs'},
        {name: 'NodeJS', value: '60', icon: 'fa-node-js'},
        {name: 'Laravel', value: '60', icon: 'fa-laravel'}],
      askills: [
        {name: 'Wordpress', value: '90', icon: 'fa-wordpress'},
        {name: 'Adobe Illustrator', value: '50', icon: 'fa-adobe'},
        {name: 'Java', value: '50', icon: 'fa-java'},
        {name: 'Linux', value: '70', icon: 'fa-linux'},
        {name: 'Git', value: '60', icon: 'fa-git-square'},
        {name: 'AWS', value: '60', icon: 'fa-aws'}],
      lang: [
        {name: 'Arabic', value: '100', icon: 'fa-language'},
        {name: 'English', value: '80', icon: 'fa-language'},
        {name: 'French', value: '80', icon: 'fa-language'},
        {name: 'Spannish', value: '30', icon: 'fa-language'}]
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () { vm.renderit = true }, 250)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
i.fab, i.fas {
    font-size: 24px;
    color: #9d7e33;
}
</style>



