<template>
<div class="col" >
    <spinner v-if='!renderit'/>
    <div class="card-profile" v-else>
      <div class="card">
          <h5 class="card-category h5">My Services</h5>
      </div>
      <div class="card-category row" style="margin: 0 auto;">
        <div class="boxc box" v-for="(service,i) in services" :key="i" style="display: inline-block;">
          <div class="title-wrapper">
            <div class="title if">
              <i :class="'fas '+service.icon"></i>
              <span class="number-bg">{{i+1}}</span>
            </div>
          </div>
          <div class="button-wrapper">
            <h3 class="card-category h3">{{service.name}}</h3>
            <p>
              {{service.value}}
            </p>
          </div>
        </div>
      </div>
      <div class="card card-body">
        <h5 class="card-category h5">My Portfolio</h5>
        <br/>
        <ul class="container-fluid float-center list-control">
          <li :class="{ active: search === '' }"><span @click="search = ''">All</span></li>
          <li :class="{ active: search === 'wbdesign' }"><span @click="search = 'wbdesign'">WEB DESING</span></li>
          <li :class="{ active: search === 'grdesign' }"><span @click="search = 'grdesign'">GRAPHIC DESING</span></li>
          <li :class="{ active: search === 'coding' }"><span @click="search = 'coding'">CODING</span></li>
        </ul>
        <br/>
        <div class="card-category row col-12" :key="search" style="margin: 0 auto;">
          <div class="col-sm-6 col-md-4 card-body bounceIn animated" v-for="(image, i) in portfolio" :key="i" style="display: inline-block;">
            <img class="imagex" :src="image" @click="onClick(i)">
          </div>
          <vue-gallery-slideshow :images="portfolio" :index="index" @close="index = null"/>
        </div>
      </div>
      <div class="card">
          <h5 class="card-category h5">My Clients</h5>
      </div>
      <carousel :per-page="2"
        :mouse-drag="true"
        :navigationEnabled="true"
        :perPageCustom="[[576, 2], [100, 1]]"
        :paginationEnabled="false"
        :navigationPrevLabel="`<i class='fas fa-angle-left'></i>`"
        :navigationNextLabel="`<i class='fas fa-angle-right'></i>`"
        :loop="true"
        >
        <slide v-for="(client,i) in clients" :key="i">
          <div class="col-12" style="display: inline-block;">
          <div class="VueCarousel box">
            <div class="title-wrapper">
              <div class="title if">
                <div class="client-img">
                  <img :src="client.icon" alt="Client">
                </div>
                <span style="float: right;">
                  <i class="fas fa-quote-right"></i>
                </span>
                <h5 class="card-category h5 text-left">{{client.name}}</h5>
                <h6 class="card-category h6 text-left">{{client.job}}</h6>
              </div>
            </div>
            <div class="button-wrapper">
              <p>
                {{client.value}}
              </p>
            </div>
          </div>
        </div>
        </slide>
      </carousel>
      <div class="card card-body">
        <h5 class="card-category h5">My Stats</h5>
        <div class="card-category row col-12" style="margin: 0 auto;">
          <div class="col-sm-6 col-lg-3 card-body" v-for="(stat,i) in stats" :key="i" style="display: inline-block;">
            <div class="">
              <div class="title-wrapper">
                <div class="title s">
                  <i :class="'fas '+stat.icon"></i>
                  <span class="number-bl">{{stat.value}}</span>
                </div>
              </div>
              <div class="button-wrapper">
                <h4 class="card-category h5">{{stat.name}}</h4>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import VueGallerySlideshow from 'vue-gallery-slideshow'
import spinner from '@/components/spinner'
export default {
  components: {
    spinner,
    VueGallerySlideshow
  },
  name: 'services',
  data () {
    return {
      renderit: false,
      search: '',
      index: null,
      portfol: [
        '/static/img/portfolio/work.png?wbdesign',
        '/static/img/portfolio/work.jpeg?wbdesign&coding',
        '/static/img/portfolio/work1.jpeg?grdesign',
        '/static/img/portfolio/work2.jpeg?grdesign&coding',
        '/static/img/portfolio/work3.jpeg?wbdesign&coding',
        '/static/img/portfolio/work4.jpeg?grdesign'
      ],
      services: [
        {
          name: 'Branding',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: 'fa-project-diagram'
        },
        {
          name: 'Clean Code',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: 'fa-code'
        },
        {
          name: 'UI/UX Design',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: 'fa-paint-brush'
        },
        {
          name: 'Seo Optimized',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: 'fa-lightbulb'
        },
        {
          name: 'Analytics',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: 'fa-poll'
        },
        {
          name: 'Marketing',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: 'fa-ad'
        }],
      clients: [
        {
          name: 'Alex Doe',
          job: 'Web Designer',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: '/static/img/clients/person-1.jpeg'
        },
        {
          name: 'Alex Doe',
          job: 'Senior Developper',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: '/static/img/clients/person-3.jpeg'
        },
        {
          name: 'Alex Doe',
          job: 'Desktop Developer',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: '/static/img/clients/person-5.jpeg'
        },
        {
          name: 'Alex Doe',
          job: 'UI/UX Designer',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: '/static/img/clients/person-2.jpeg'
        },
        {
          name: 'Alex Doe',
          job: 'SEO Manager',
          value: 'Lorem Ipsum is simply dummy text of the printing and typesetting industry.',
          icon: '/static/img/clients/person-4.jpeg'
        }],
      stats: [
        {
          name: 'Project',
          value: '60',
          icon: 'fa-building'
        },
        {
          name: 'Coffee Cup',
          value: '600',
          icon: 'fa-coffee'
        },
        {
          name: 'Code Line',
          value: '15k',
          icon: 'fa-terminal'
        },
        {
          name: 'Client',
          value: '50',
          icon: 'fa-user'
        }]
    }
  },
  computed: {
    portfolio () {
      return this.portfol.filter(item => {
        return item.indexOf(this.search.toLowerCase()) > -1
      })
    }
  },
  methods: {
    onClick (i) {
      this.index = i
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () { vm.renderit = true }, 250)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



