<template>
  <div class="col">
    <spinner v-if="!renderit"/>
    <div class="card" v-else>
      <div class="card-body">
        <h5 class="card-category h5">Get In Touch</h5>
        <div class="card-description row">
          <div class="col-lg-4 col-sm-6 col-12" style="padding-top: 2rem;">
            <div
              class="col-12 card-body"
              v-for="info in infos"
              :key="info.name"
              style="display: inline-block;"
            >
              <div class="text-left">
                <div class="title-wrapper">
                  <div class="title">
                    <i :class="'fas '+info.icon"></i>
                    <span class="number-blj">{{info.name}}</span>
                  </div>
                </div>
                <div class="button-wrapper">
                  <h5 class="card-category h5">{{info.value}}</h5>
                </div>
              </div>
            </div>
          </div>
          <div class="col-lg-8 col-sm-6 col-12">
            <form class="form" @submit="send">
                <div id="contact-status" v-html="status"></div>
                <div class="form-group">
                <label class="form-label text-left" for="input-example-1">Name</label>
                <input class="form-input" type="text" model="contact.name" placeholder="Name" required>
                </div>
                <div class="form-group">
                <label class="form-label text-left" for="input-example-2">Email</label>
                <input class="form-input" type="email" model="contact.mail" placeholder="Email" required>
                </div>
                <div class="form-group">
                <label class="form-label text-left" for="input-example-3">Message</label>
                <textarea class="form-input" model="contact.msg" placeholder="Textarea" rows="5" required></textarea>
                </div>
                <div class="form-group">
                <button class="bttn-float bttn-warning input-group-btn" type="submit">Submit</button>
                </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import spinner from '@/components/spinner'
export default {
  components: { spinner },
  name: 'contact',
  data () {
    return {
      renderit: false,
      status: null,
      contact: {
        mail: '',
        msg: '',
        name: ''
      },
      infos: [
        { name: 'Email', value: 'admin@fedi.pw', icon: 'fa-envelope' },
        { name: 'Phone', value: 'xxx-xxxxx', icon: 'fa-phone-square' },
        {
          name: 'Address',
          value: 'Lorem ipsum dolor sit amet',
          icon: 'fa-home'
        }
      ]
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () {
      vm.renderit = true
    }, 250)
  },
  methods: {
    send (evt) {
      let form = new FormData()
      let vm = this
      evt.preventDefault()
      for (let field in this.contact) {
        form.append(field, this.contact[field])
      }
      this.$http.post('/app.php', form).then(response => {
        vm.status = '<div class="toast toast-success">Your message has been sent. Thank you!</div>'
        this.clearForm()
      }).catch(e => {
        vm.status = '<div class="toast toast-warning">Oops !! Something Went Wrong</div>'
      })
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



