<template>
  <div id="app">
    <div :class="theme+'-edition main-panel'">
      <div class="content">
        <div class="container-fluid">
          <div class="row">
            <div class="col-xl-8 col-md-12 col-lg-12" style="margin: 0 auto;">
              <div class="container-fluid">
                <div id="cntr" class="fancy-box-wrapper row">
                  <spinner v-if='!renderit'/>
                  <div class="box-wrapper" v-else>
                    <div class="col">
                      <nav class="card footer">
                          <ul class="container-fluid float-center">
                            <li v-for="(item,i) in social" :key="i">
                              <a :href="item.value"><i :class="'fab '+item.icon"></i></a>
                            </li>
                          </ul>
                      </nav>
                    </div>
                    <div class="col" v-if="this.$route.name !== 'Themayos'">
                      <div class="card card-profile">
                        <div class="card-avatar">
                          <img class="img" src="/static/img/fedirjh.jpeg">
                        </div>
                        <div class="card-body">
                          <h3 class="card-title h3">{{infos[0].value}}</h3>
                          <h4 class="card-category h4">{{status}}</h4>
                        </div>
                      </div>
                    </div>
                    <div class="col" v-if="this.$route.name !== 'Themayos'">
                      <div class="card">
                        <div class="card-body">
                          <dl class="dl-horizontal clear-mrg row col-md-10" style="margin: 0 auto;">
                            <div v-for="info in infos" :key="info.name" class="col-md-4 col-sm-6">
                              <div class="card-title h5 text-left">
                                <i :class="'fas '+info.icon"></i>  {{info.name}} : {{info.value}}
                              </div>
                            </div>
                          </dl>
                        </div>
                      </div>
                    </div>
                    <div class="col" v-if="this.$route.name !== 'Onepagedark' && this.$route.name !== 'Onepagelight' && this.$route.name !== 'Themayos'">
                      <nav class="card footer">
                        <ul class="container-fluid float-center">
                          <li><router-link to="/" class="ali"><i class="material-icons">account_box</i> About</router-link></li>
                          <li><router-link to="/skills" class="ali"><i class="material-icons">stars</i> Skills</router-link></li>
                          <li><router-link to="/experiences" class="ali"><i class="material-icons">verified_user</i> Experiences</router-link></li>
                          <li><router-link to="/services" class="ali"><i class="material-icons">work</i> Services</router-link></li>
                          <li><router-link to="/contact" class="ali"><i class="material-icons">email</i> Contact</router-link></li>
                        </ul>
                      </nav>
                    </div>
                    <router-view/>
                    <back-to-top visibleoffset="100">
                      <i class="fas fa-arrow-circle-up" style="font-size: 30px;"></i>
                    </back-to-top>
                    <div class="col">
                      <nav class="card footer">
                          <ul class="container-fluid float-center">
                            <li v-for="(item,i) in social" :key="i">
                              <a :href="item.value"><i :class="'fab '+item.icon"></i></a>
                            </li>
                          </ul>
                          <h5 class="card-category h5">2019 © All Rights Reserved By <a href="https://fedi.pw/">fedirjh</a></h5>
                      </nav>
                    </div>
                  </div>
                </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import spinner from '@/components/spinner'
export default {
  components: {spinner},
  name: 'App',
  data () {
    if (this.$route.name === 'Light') {
      localStorage.theme = 'light'
    } else if (this.$route.name === 'Dark') {
      localStorage.theme = 'dark'
    }
    if (!localStorage.theme) {
      localStorage.theme = 'dark'
    }
    return {
      theme: localStorage.theme,
      renderit: false,
      status: 'CEO / Co-Founder / Web Developper',
      infos: [
        {name: 'Name', value: 'Fedi Rajhi', icon: 'fa-user-circle'},
        {name: 'Age', value: '23', icon: 'fa-user'},
        {name: 'Phone', value: '+21611223344', icon: 'fa-phone-square'},
        {name: 'Address', value: 'Tunisia', icon: 'fa-map-marked-alt'},
        {name: 'Nationality', value: 'Tunisian', icon: 'fa-globe-africa'},
        {name: 'Freelance', value: 'Available', icon: 'fa-briefcase'}],
      social: [
        {value: 'https://twitter.com/fedi_rajhi', icon: 'fa-twitter-square'},
        {value: 'https://www.facebook.com/fedirajhi', icon: 'fa-facebook-square'},
        {value: 'https://www.instagram.com/fedirjh', icon: 'fa-instagram'},
        {value: 'https://github.com/fedirjh', icon: 'fa-github-square'},
        {value: 'https://www.linkedin.com/in/fedirajhi', icon: 'fa-linkedin'},
        {value: 'https://www.youtube.com/c/fedi96 ', icon: 'fa-youtube-square'},
        {value: '#', icon: 'fa-google-play'}]
    }
  },
  mounted () {
    let vm = this
    setTimeout(function () { vm.renderit = true }, 500)
  },
  watch: {
    theme (theme) {
      localStorage.theme = theme
    }
  }
}
</script>

<style>
.card-avatar {
    margin: 10px auto 0 !important;
}
.card.card-profile {
    background: url(assets/wall.jpeg) center no-repeat;
    background-size: cover;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
}
.fancy-box-wrapper .box-wrapper {
  overflow: visible !important;
  }
.fancy-box-wrapper .box-wrapper {
  padding-bottom: 0 !important;
  }
.card-profile .card-avatar {
    max-width: 145px;
    max-height: 145px;
    border: 2px #020f1e solid;
  }
i.fab {
  font-size: 24px;
  }

.ali{
  padding: .3rem !important;
  }
.col-md-6.m-left {
  padding-left: 20%;
  }
i.fas {
  font-size: 20px;
  color: #9d7e33;
  }
.h5 i {
  margin-right: 0.96rem;
  width: 6%;
  text-align: center;
  }
a.ali::before {
    position: absolute;
    content: '';
    width: calc(100% - 57px);
    display: table;
    height: 1px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 3px;
    bottom: 12px;
    right: 5px;
    -webkit-transition: all 300ms linear;
    transition: all 300ms linear;
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
    transform: scaleX(0);
    background: #e5b12f !important;
    opacity: 0;
}
a.ali.router-link-exact-active.router-link-active::before {
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
    opacity: 1;
    color: #f3c26b !important;
    background-color: transparent;
    text-decoration: none;
}
a.ali.router-link-exact-active.router-link-active {
  opacity: 1;
}
</style>



